				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="" prop="jiaoshigonghao">
					<el-input v-model="ruleForm.jiaoshigonghao"  placeholder="" clearable  :readonly="ro.jiaoshigonghao"></el-input>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' v-else class="input" label="" prop="jiaoshigonghao">
					<el-input v-model="ruleForm.jiaoshigonghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="" prop="jiaoshixingming">
					<el-input v-model="ruleForm.jiaoshixingming"  placeholder="" clearable  :readonly="ro.jiaoshixingming"></el-input>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' v-else class="input" label="" prop="jiaoshixingming">
					<el-input v-model="ruleForm.jiaoshixingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="" prop="xueshengzhanghao">
					<el-input v-model="ruleForm.xueshengzhanghao"  placeholder="" clearable  :readonly="ro.xueshengzhanghao"></el-input>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' v-else class="input" label="" prop="xueshengzhanghao">
					<el-input v-model="ruleForm.xueshengzhanghao" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="" prop="xueshengxingming">
					<el-input v-model="ruleForm.xueshengxingming"  placeholder="" clearable  :readonly="ro.xueshengxingming"></el-input>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' v-else class="input" label="" prop="xueshengxingming">
					<el-input v-model="ruleForm.xueshengxingming" placeholder="" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' class="upload" v-if="type!='info'&& !ro.tijiaozuoye" label="" prop="tijiaozuoye">
					<file-upload
						tip=""
						action="file/upload"
						:limit="1"
						:type="3"
						:multiple="true"
						:fileUrls="ruleForm.tijiaozuoye?ruleForm.tijiaozuoye:''"
						@change="tijiaozuoyeUploadChange"
					></file-upload>
				</el-form-item>  
				<el-form-item :style='{"margin":"0 0 20px 0"}' v-else-if="ruleForm.tijiaozuoye" label="" prop="tijiaozuoye">
					<el-button :style='{"border":"0","cursor":"pointer","padding":"0 15px","margin":"0 20px 0 0","color":"rgba(255, 255, 255, 1)","outline":"none","borderRadius":"4px","background":"#6aab9c","width":"auto","lineHeight":"40px","fontSize":"14px","fontWeight":"400","height":"40px"}' type="text" size="small" @click="download($base.url+ruleForm.tijiaozuoye)"></el-button>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' v-else-if="!ruleForm.tijiaozuoye" label="" prop="tijiaozuoye">
					<el-button :style='{"border":"0","cursor":"pointer","padding":"0 15px","margin":"0 20px 0 0","color":"rgba(255, 255, 255, 1)","outline":"none","borderRadius":"4px","background":"#6aab9c","width":"auto","lineHeight":"40px","fontSize":"14px","fontWeight":"400","height":"40px"}' type="text" size="small"></el-button>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' class="date" v-if="type!='info'" label="" prop="tijiaoshijian">
					<el-date-picker
						value-format="yyyy-MM-dd HH:mm:ss"
						v-model="ruleForm.tijiaoshijian" 
						type="datetime"
						:readonly="ro.tijiaoshijian"
						placeholder=""
					></el-date-picker>
				</el-form-item>
				<el-form-item :style='{"margin":"0 0 20px 0"}' class="input" v-else-if="ruleForm.tijiaoshijian" label="" prop="tijiaoshijian">
